@import "vars";
@import "mixins";
@import "fonts";
@import "settings";


// Header

.header {
  padding-top: 16px;
  padding-bottom: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 0.3s ease 0s;
  z-index: 99;
  // .header__container

  &__container {}

  // .header__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center
  }

  // .header__burger

  &__burger {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: #9B9B9B;
    @media (max-width: 768px) {
      font-size: 14px;
    }
    @media (max-width: 576px) {
      font-size: 0;
    }
    &-wr {
      background-color: #384D68;
      border-radius: 4px;
      width: 45px;
      height: 45px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 24px;
      cursor: pointer;
      transition: all 0.3s ease 0s;
      &:hover {
        box-shadow: 0 0 10px rgba(255,255,255,0.2);
      }
      @media (max-width: 768px) {
        margin-right: 18px;
      }
      @media (max-width: 576px) {
        width: 37px;
        height: 37px;
      }
    }
  }

  // .header__logo

  &__logo {
    @media (max-width: 576px) {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        max-width: 60%;
      }
    }
  }

  // .header__phone

  &__phone {
    display: flex;
    align-items: center;
    span {
      > a {
        font-weight: 500;
        font-size: 16px;
        line-height: 18px;
        text-transform: uppercase;
        color: #9B9B9B;
        margin-right: 24px;
        @media (max-width: 768px) {
          font-size: 14px;
          margin-right: 18px;
        }
        @media (max-width: 576px) {
          font-size: 0;
        }
      }
    }
    a:nth-child(2) {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #384D68;
      border-radius: 4px;
      width: 45px;
      height: 45px;
      transition: all 0.3s ease 0s;
      &:hover {
        box-shadow: 0 0 10px rgba(255,255,255,0.2);
      }
      @media (max-width: 576px) {
        width: 37px;
        height: 37px;
      }
    }
  }

  // .header__nav

  &__nav {
     position: absolute;
     background-color: var(--blueBg);
     top: 0;
     right: 0;
     width: 40%;
     height: 100vh;
     transform: translateX(100%);
     transition: all 0.3s ease 0s;
     box-shadow: 0 0 7px rgba(255,255,255,0.4);
     &.menu--active {
      transform: translateY(0);
    }
    > ul {
      padding: 0;
      margin: 0;
      list-style: none;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 50px;
      > li {
        width: 100%;
        text-align: center;
        border-bottom: 1px solid rgba(255,255,255,0.2);
        > a {
          display: block;
          width: 100%;
          padding: 10px 0;
          text-transform: uppercase;
          font-size: 22px;
          color: var(--primary);
          transition: all 0.3s ease 0s;
          &:hover {
            background-color: #0fa2ec;
          }
          @media (max-width: 768px) {
            font-size: 18px;
          }
          @media (max-width: 576px) {
            font-size: 15px;
          }
        }
      }
    }
    @media (max-width: 768px) {
      width: 50%;
    }
    @media (max-width: 576px) {
      width: 70%;
    }
  }

  .burger {
    @include burger;
    // .burger__line
    &__line {}
  }
  &.fixedMenu {
    box-shadow: 3px 3px 7px rgba(0,0,0,0.5);
    background-color: #0f1926;
    z-index: 99;
    padding-top: 0;
    padding-bottom: 0;
  }
}

// Promo Section

.promo {
  padding-top: 150px;
  // .promo__wrap
  .header__container {
    @media (max-width: 480px) {
      padding: 0;
    }
  }

  &__wrap {
    display: flex;
    gap: 20px;
    @media (max-width: 992px) {
      flex-direction: column-reverse;
    }
    img {
      flex: 0 1 50%;
      transform: translateX(96px);
      @media (max-width: 1500px) {
        transform: translateX(0px);
      }
      @media (max-width: 1400px) {
        flex: 0 1 40%;
      }
      @media (max-width: 1200px) {
        flex-basis: 30%;
      }
      @media (max-width: 992px) {
        flex-basis: 100%;
        max-width: 70%;
        display: block;
        margin: 0 auto;
      }
      @media (max-width: 576px) {
        max-width: 100%;
      }
    }
  }

  // .promo__left

  &__left {
    flex: 0 1 50%;
    padding-top: 200px;
    padding-left: 125px;
    @media (max-width: 1400px) {
      padding-left: 45px;
      padding-top: 120px;
      flex: 0 1 60%;
    }
    @media (max-width: 1200px) {
      padding-top: 100px;
      padding-left: 20px;
      flex-basis: 70%;
    }
    @media (max-width: 992px) {
      flex-basis: 100%;
      padding-top: 0;
    }
    @media (max-width: 768px) {
      padding-left: 0;
    }
  }

  // .promo__title

  &__title {
    font-size: 64px;
    line-height: 64px;
    max-width: 560px;
    @media (max-width: 1200px) {
      font-size: 50px;
      line-height: 60px;
    }
    @media (max-width: 768px) {
      margin-top: 15px;
      font-size: 40px;
      line-height: 50px;
    }
    @media (max-width: 576px) {
      font-size: 32px;
      line-height: 37px;
      text-align: center;
    }
  }

  // .promo__subtitle

  &__subtitle {
    margin-top: 40px;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: var(--primary);
    @media (max-width: 1200px) {
      font-size: 20px;
      line-height: 28px;
      margin-top: 30px;
    }
    @media (max-width: 576px) {
      font-size: 15px;
      line-height: 22px;
      text-align: center;
    }
  }

  // .promo__btn

  &__btn {
    margin-top: 48px;
    @media (max-width: 768px) {
      padding: 12px 25px;
    }
    @media (max-width: 576px) {
      padding: 12px 15px;
      display: block;
      max-width: fit-content;
      margin: 25px auto 0 auto;
    }
  }

  @media (max-width: 768px) {
    padding-top: 130px;
  }
  @media (max-width: 576px) {
    padding-top: 100px;
  }
}


// Count Section

.count {
  padding-top: 100px;
  padding-bottom: 250px;
  @media (max-width: 1400px) {
    padding-bottom: 125px;
  }
  @media (max-width: 768px) {
    padding-bottom: 80px;
  }
  @media (max-width: 576px) {
    padding-top: 50px;
    padding-bottom: 40px;
  }
  // .count__wrap
  .header__container {
    @media (max-width: 480px) {
      padding: 0;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    @media (max-width: 1200px) {
      flex-direction: column;
    }
    @media (max-width: 1400px) {
      justify-content: center;
    }
  }

  // .count__img

  &__img {
    flex: 0 1 40%;
    //max-width: 620px;
    img {
      margin-top: 85px;
    }
    @media (max-width: 1400px) {
      flex-basis: 50%;
      img {
        width: 100%;
      }
    }
    @media (max-width: 1300px) {
      flex-basis: 60%;
    }
    @media (max-width: 1200px) {
      img {
        max-width: 50%;
        display: block;
        margin: 0 auto;
        margin-top: 40px;
        margin-bottom: 40px;
      }
    }
    @media (max-width: 576px) {
      img {
        max-width: 90%;
      }
    }
  }

  // .count__name

  &__title {

  }

  // .count__image

  &__image {}

  // .count__info

  &__info {
    flex: 0 1 60%;
    @media (max-width: 1400px) {
      flex-basis: 50%;
    }
    @media (max-width: 1300px) {
      flex-basis: 40%;
    }
  }

  // .count__text

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: var(--primary);
    padding-left: 160px;
    @media (max-width: 1300px) {
      padding-left: 0;
    }
    @media (max-width: 768px) {
      font-size: 15px;
      line-height: 22px;
    }
  }

  // .count__block

  &__block {
    margin-top: 155px;
    @media (max-width: 1550px) {
      margin-top: 200px;
    }
    @media (max-width: 1350px) {
      margin-top: 150px;
    }
    @media (max-width: 1300px) {
     margin-top: 100px;
    }
    @media (max-width: 1200px) {
      margin-top: 50px;
    }
    @media (max-width: 576px) {
      margin-top: 30px;
    }
  }

  // .count__btn

  &__btn {}

  .block-count {
    padding-left: 160px;
    padding-top: 100px;
    padding-bottom: 120px;
    background-color: var(--blueBg);
    @media (max-width: 1550px) {
      padding-left: 50px;
    }
    @media (max-width: 1300px) {
      padding-top: 50px;
      padding-bottom: 40px;
    }
    @media (max-width: 768px) {
      padding: 15px;
    }
    // .block-count__wrap

    &__wrap {
      display: flex;
      flex-wrap: wrap;
      row-gap: 50px;
      @media (max-width: 1300px) {
        row-gap: 20px;
      }

    }

    // .block-count__item

    &__item {
      flex-basis: 50%;
      @media (max-width: 1300px) {
        flex-basis: 100%;
      }
      @media (max-width: 1200px) {
        flex-basis: 50%;
      }
      @media (max-width: 576px) {
        flex-basis: 100%;
        text-align: center;
      }
    }

    // .block-count__num

    &__num {
      font-weight: 500;
      font-size: 80px;
      line-height: 100px;
      color: var(--primary);
      @media (max-width: 1300px) {
        font-size: 60px;
        line-height: 70px;
      }
      @media (max-width: 768px) {
        font-size: 50px;
        line-height: 60px;
      }
      @media (max-width: 576px) {
       font-size: 32px;
       line-height: 37px;
      }
    }

    // .block-count__descr

    &__descr {
      font-weight: 400;
      font-size: 24px;
      line-height: 36px;
      color: var(--primary);
      opacity: 0.7;
      @media (max-width: 768px) {
        font-size: 20px;
        line-height: 28px;
      }
      @media (max-width: 576px) {
        font-size: 16px;
        line-height: 24px;
      }
    }

    // .block-count__btns

    &__btns {
      margin-top: 100px;
      a:first-child {
        margin-right: 25px;
        @media (max-width: 1300px) {
          margin-right: 0;
          margin-bottom: 20px;
        }
        @media (max-width: 1200px) {
          margin-right: 25px;
          margin-bottom: 0px;
        }
        @media (max-width: 576px) {
          margin-right: 15px;
          margin-bottom: 0px;
        }
        @media (max-width: 480px) {
          margin-right: 0px;
          margin-bottom: 15px;
        }
      }

      @media (max-width: 1300px) {
        margin-top: 40px;
      }
      @media (max-width: 1300px) {
        margin-top: 80px;
      }
      @media (max-width: 768px) {
        text-align: center;
        margin-top: 40px;
      }
      @media (max-width: 480px) {
        a {
          display: block;
          max-width: 100%;
        }
      }
    }
  }
}

// Section sensibus

.sensibus {
  padding-top: 180px;
  padding-bottom: 300px;
  position: relative;
  @media (max-width: 1200px) {
    padding-top: 150px;
    padding-bottom: 270px;
  }
  @media (max-width: 576px) {
    padding-top: 80px;
  }
  // .sensibus__title

    &__title {
    max-width: 650px;
  }

  // .sensibus__img

  &__img {
    position: absolute;
    top: calc(50% - 100px);
    right: 0;
    transform: translateY(-50%);
    @media (max-width: 1400px) {
      max-width: 70%;
    }
    @media (max-width: 1200px) {
      max-width: 60%;
      top: calc(50% - 50px);
    }
    @media (max-width: 992px) {
      top: calc(50% + 50px);
    }
    @media (max-width: 768px) {
      display: none;
    }
  }

  // .sensibus__btn

  &__btn {
    margin-top: 40px;
    @media (max-width: 768px) {
      padding: 12px 25px;
    }
    @media (max-width: 576px) {
      padding: 12px 15px;
      display: block;
      max-width: fit-content;
      margin: 25px auto 0 auto;
    }
  }

  .search {
    transform: translateY(0);
    position: absolute;
    bottom: 0;
    form {
      max-width: 1530px;
      margin: 0 auto;
    }
    select {
      border: 1px solid var(--blueBg);
      border-radius: 4px;
    }
  }
}


// Section property
.property {
  padding-top: 150px;
  padding-bottom: 180px;
  @media (max-width: 1200px) {
    padding-bottom: 100px;
  }
  @media (max-width: 768px) {
    padding-top: 80px;
  }
  @media (max-width: 576px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  // .property__wrap

  &__wrap {}

  // .property__head

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    nav {
      > ul {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        @media (max-width: 768px) {
          margin-top: 35px;
        }
        > li {
          font-weight: 500;
          line-height: 16px;
          text-transform: uppercase;
          color: #A4B1C4;
          margin-right: 60px;
          position: relative;
          transition: all 0.3s ease 0s;
          cursor: pointer;
          @media (max-width: 576px) {
            margin-right: 30px;
            font-size: 14px;
          }
          @media (max-width: 400px) {
            margin-right: 20px;
            font-size: 13px;
          }
          &:last-child {
            margin-right: 0;
          }
          &:hover {
            color: var(--primary);
          }
          &.active {
            color: var(--primary);
            &:after {
              content: '';
              position: absolute;
              left: 0;
              bottom: -15px;
              width: 100%;
              height: 4px;
              background-color: var(--btnBg);
              @media (max-width: 576px) {
                height: 2px;
                bottom: -7px;
              }
            }
          }
        }
      }
    }
    @media (max-width: 768px) {
      flex-direction: column;
    }
  }

  // .property__title

  &__title {}

  // .property__content

  &__content {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 50px;
    column-gap: 30px;
    @media (max-width: 1600px) {
      justify-content: center;

    }
    @media (max-width: 576px) {
      row-gap: 30px;
      margin-top: 30px;
    }
  }

  // .property__item

  &__item {
    max-width: 360px;
    //min-height: 400px;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    @media (max-width: 768px) {
      max-width: 80%;
    }
    @media (max-width: 576px) {
      max-width: 100%;
    }
    &:hover:not(.property__item:first-child, .property__item:nth-child(2)) .property__info {
      opacity: 1;
      padding: 20px;
      @media (max-width: 400px) {
        padding: 10px;
      }
      .property__name {
        font-size: 20px;
        line-height: 22px;
        color: var(--primary);
        @media (max-width: 576px) {
          font-size: 25px;
          line-height: 30px;
        }
        @media (max-width: 400px) {
          font-size: 20px;
          line-height: 22px;
          max-width: 100%;
        }
      }
      .property__text {
        margin-top: 15px;
        font-size: 14px;
        line-height: 20px;
        @media (max-width: 576px) {
          font-size: 14px;
          line-height: 20px;
          margin-top: 15px;
        }
        @media (max-width: 400px) {
          font-size: 12px;
          line-height: 17px;
          margin-top: 7px;
        }
      }
      .property__price {
        margin-top: 20px;
        font-weight: 500;
        font-size: 25px;
        line-height: 24px;
         @media (max-width: 576px) {
          font-size: 25px;
          line-height: 20px;
          margin-top: 25px;
        }
        @media (max-width: 400px) {
          margin-top: 10px;
          font-size: 20px;
        }
      }
      .property__btns {
        margin-top: 20px;
        @media (max-width: 768px) {
          margin-top: 60px;
        }
        @media (max-width: 576px) {
          margin-top: 20px;
        }
        @media (max-width: 400px) {
          margin-top: 15px;
        }

      }
      .property__btn {
        padding: 7px 25px;
        font-size: 13px;
        transition: none;
        &:first-child {
          margin-right: 15px;
          /* @media (max-width: 576px) {
            margin-right: 15px;
          } */
        }
        @media (max-width: 576px) {
         padding: 5px 15px;
         font-size: 13px;
        }
        @media (max-width: 400px) {
          padding: 5px 10px;
          font-size: 12px;
        }
      }
    }
    &:first-child {
      max-width: 750px;
      @media (max-width: 1600px) {
        max-width: 600px;
      }
      @media (max-width: 1300px) {
        max-width: 450px;
      }
      @media (max-width: 992px) {
        max-width: 80%;
      }
      @media (max-width: 576px) {
        max-width: 100%;
      }
      &:hover .property__info {
        opacity: 1;
      }
    }
    &:nth-child(2) {
      max-width: 750px;
      @media (max-width: 1600px) {
        max-width: 600px;
      }
      @media (max-width: 1300px) {
        max-width: 450px;
      }
      @media (max-width: 992px) {
        max-width: 80%;
      }
      @media (max-width: 576px) {
        max-width: 100%;
      }
      &:hover .property__info {
        opacity: 1;
      }
    }
    img {
      height: 100%;
      object-fit: cover;
    }
    &.hide {
      order: 100;
    }
  }

  // .property__img

  &__img {
    height: 100%;
  }

  // .property__info

  &__info {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    background-color: rgba(0,0,0,0.8);
    padding-top: 45px;
    padding-left: 55px;
    opacity: 0;
    transition: all 0.3s ease 0s;
    @media (max-width:1300px) {
      padding: 20px;
    }
    @media (max-width: 768px) {
      padding: 20px;
    }
    @media (max-width: 576px) {
      padding: 15px;
    }
    @media (max-width: 400px) {
      padding: 10px;
    }
  }

  // .property__name

  &__name {
    font-weight: 600;
    font-size: 30px;
    line-height: 36px;
    color: var(--primary);
    max-width: 280px;
    @media (max-width: 576px) {
      font-size: 25px;
      line-height: 30px;
    }
    @media (max-width: 400px) {
      font-size: 20px;
      line-height: 22px;
      max-width: 100%;
    }
  }

  // .property__text

  &__text {
    margin-top: 24px;
    max-width: 512px;
    font-weight: 400;
    line-height: 24px;
    color: var(--primary);
    @media (max-width: 576px) {
      font-size: 14px;
      line-height: 20px;
      margin-top: 15px;
    }
    @media (max-width: 400px) {
      font-size: 12px;
      line-height: 17px;
      margin-top: 7px;
    }
  }

  // .property__price

  &__price {
    margin-top: 40px;
    font-weight: 500;
    font-size: 32px;
    line-height: 24px;
    color: var(--primary);
    @media (max-width: 1300px) {
      margin-top: 20px;
      font-weight: 500;
      font-size: 25px;
      line-height: 24px;
    }
    @media (max-width: 576px) {
      font-size: 25px;
      line-height: 20px;
      margin-top: 25px;
    }
    @media (max-width: 400px) {
      margin-top: 10px;
      font-size: 20px;
    }
  }

  // .property__btns

  &__btns {
    margin-top: 140px;
    @media (max-width: 1600px) {
      margin-top: 60px;
    }
    @media (max-width: 1300px) {
      margin-top: 20px;
    }
    @media (max-width: 992px) {
      margin-top: 80px;
    }
    @media (max-width: 768px) {
      margin-top: 25px;
    }
    /* @media (max-width: 768px) {
      margin-top: 60px;
    } */
    @media (max-width: 576px) {
      margin-top: 20px;
    }
    @media (max-width: 400px) {
      margin-top: 20px;
    }

  }

  // .property__btn

  &__btn {
    &:first-child {
      margin-right: 25px;
      @media (max-width: 576px) {
        margin-right: 15px;
      }
      &:hover {
        background-color: transparent;
      }
    }
    @media (max-width: 1300px) {
      padding: 7px 25px;
      font-size: 13px;
      transition: none;
      &:first-child {
        margin-right: 15px;
